import React from 'react';
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Search from './taobao/Search';
import List from './taobao/List';
import Mine from './taobao/Mine';
import Detail1 from './taobao/Detail1';
import Detail2 from './taobao/Detail2';
import Detail3 from './taobao/Detail3';
import Detail4 from './taobao/Detail4';
import Detail5 from './taobao/Detail5';
import Detail6 from './taobao/Detail6';
import Detail7 from './taobao/Detail7';
import Detail8 from './taobao/Detail8';
import Detail9 from './taobao/Detail9';
import Detail10 from './taobao/Detail10';
import { Provider } from 'react-redux';
import store from './reduxdemo.js/store';

const App = ()=>{
	return <Provider store = {store}>
		<Router>
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/cart' component={Cart} />
				<Route path='/list' component={List} />
				<Route path='/mine' component={Mine} />
				<Route path='/home/detail1' component={Detail1} />
				<Route path='/home/detail2' component={Detail2} />
				<Route path='/home/detail3' component={Detail3} />
				<Route path='/home/detail4' component={Detail4} />
				<Route path='/home/detail5' component={Detail5} />
				<Route path='/home/detail6' component={Detail6} />
				<Route path='/home/detail7' component={Detail7} />
				<Route path='/home/detail8' component={Detail8} />
				<Route path='/home/detail9' component={Detail9} />
				<Route path='/home/detail10' component={Detail10} />
				<Route path='/home/search' component={Search} />
				
				<Route render={()=><Redirect to='/'/>} />
				
			</Switch>
			<Mytab />
		</Router>
	</Provider>
}
export default App;
