class Cart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            reducers: ''
        }
    }
    render() {
        function del(id){
            del_brand(id)
        }
        const list = store.getState();
        console.log(list);
        return list.map((item) => {
            return <div>
                {item}
                <button onClick={del(item)}>删除</button>
            </div>
        })
    }

}
export default connect(store => store)(Cart);