import React from 'react'
import Mytab2 from './Mytab2'

const Detail8 = (props) => {
    return (
        <div>
            <img 
                src={require("../assets/return.png")}
                onClick={()=>{
                    props.history.goBack();
                }}
                style= {{
                    width:"5%",
                    height:"5%",
                    margin:"1% 0 0 1%"
                }}
            />
            <img  
                style={{
                    width:"80%",
                    padding:"5% 10% 0 10%"
                }} 
                src={require("../assets/detail8.jpg")}
            />
            <Mytab2 id={'shop8'}/>
        </div>
    )
}

export default Detail8