import React from 'react'
import './header.css';
import {withRouter} from 'react-router-dom';

const Header = (props) => {
    return (
        <div className='search-div' onClick={()=>{
                props.history.push('./home/search')
            }}
        >
            <div className='contain'>
                <img src={require('../assets/search.png')} className='icon-search' alt=""/>
                <span>寻找宝贝店铺</span>
            </div>
        </div>
    )
}

export default withRouter(Header)