import React, { Component } from 'react'
import './mytab2.css'
import { connect } from 'react-redux';
import store from '../reduxdemo.js/store';
import { add_brand,get_brands } from '../reduxdemo.js/actions';

class Mytab2 extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            reducers:''
        }
    }
    render() {
        return (
            <div>
                <div className="box">
                    <div className="dks" >
                        <a href="#">
                            <i className="iconfont icon-dianpu" style={{fontSize:28+'px',marginBottom:8+'px'}}></i>
                            <br/>店铺
                        </a>
                    </div>
                    <div className="dks" >
                        <a href="#">
                            <i className="iconfont icon-wangwang1" style={{fontSize:28+'px',marginBottom:8+'px'}}></i>
                            <br/>客服
                        </a>
                    </div>
                    <div className="dks" >
                        <a href="#">
                            <i className="iconfont icon-shoucang" style={{fontSize:28+'px',marginBottom:8+'px'}} onClick={(e) =>{
                                    let d = e.currentTarget;
                                    d.setAttribute('class','iconfont icon-shoucang-b-copy');
                                    this.shouCang()
                                }}></i>
                            <br/>收藏
                        </a>
                    </div>
                    <div id="jiaru" >
                        <a href="#">
                            加入购物车
                        </a>
                    </div>
                    <div id="goumai" >
                        <a href="#">
                            立即购买
                        </a>
                    </div>
                </div>
            </div>
        )
    }
    shouCang(){
        let id = this.props.id;
        console.log(id);
        this.props.dispatch(add_brand(id));
    }
}

export default connect(store=>store)(Mytab2);
