const shoucang = [];

function reducers(state=shoucang,action){
    switch(action.type){
        
        case 'add_brand':
            
            const newAarr = [...state];
            newAarr.push(action.data);
            return newAarr;
            
        case 'del_brand':
            const newArr = state.filter(item=>item.id!==action.data);
            return newArr;
            
        case 'get_brands':
            return action.data;
            
        default:
            return state;
    }
}

export default reducers;